<template>
  <b-modal :id="id" title="Shipment Item" @ok="saveShipmentItem" size="lg">
    <b-row>
      <b-col sm="12">
        <b-form-group>
          <label>Note</label>
          <b-form-input type="text" v-model="shipmentItem.note"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="12">
        <b-form-group>
          <label>Address</label>
          <b-form-input type="text" v-model="shipmentItem.address"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="12">
        <b-form-group>
          <label>Phone</label>
          <b-form-input type="text" v-model="shipmentItem.phone"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
  </b-modal>
</template>

<script>
export default {
  name: "OrderDetail",
  props: ["id", "item"],
  components: {},
  data() {
    return {
      shipmentItem: {}
    };
  },
  watch: {
    item: function(newValue) {
      this.shipmentItem = newValue || {};
    }
  },
  mounted: function() {},
  computed: {},
  methods: {
    saveShipmentItem: function() {
      this.$emit("saveShipmentItem", this.shipmentItem);
      this.$bvModal.hide(this.id);
    }
  }
};
</script>
