firebase.initializeApp(firebaseConfig);

// firebase utils
const db = firebase.firestore()
const auth = firebase.auth()
const currentUser = auth.currentUser

//date issue fix according to firebase
//db.settings(settings)

// firebase collections
const productCollection = db.collection('products')
const customerCollection = db.collection('customers')
const orderCollection = db.collection('orders')
const counterCollection = db.collection('counter')

let counterInstance = undefined;
getCounterInstance();
function getCounterInstance() {
    return new Promise(function (resolve) {
        if (counterInstance) resolve(counterInstance);
        else {
            counterCollection.limit(1).onSnapshot(snapshot => snapshot.forEach(doc => {
                counterInstance = doc.data();
                resolve(counterInstance);
            }));
        }
    });

}

const counterService = {
    getCustomerCount: async function () {
        await getCounterInstance();
        return counterInstance.customerCount;
    },
    increaseCustomer: async function () {
        await getCounterInstance();
        counterInstance.customerCount++;
        counterCollection.doc(counterInstance.id).set(counterInstance);
        return counterInstance.customerCount;
    },
    decreaseCustomer: async function () {
        await getCounterInstance();
        counterInstance.customerCount--;
        counterCollection.doc(counterInstance.id).set(counterInstance);
        return counterInstance.customerCount;
    },
    getProductCount: async function () {
        await getCounterInstance();
        return counterInstance.productCount;
    },
    increaseProduct: async function () {
        await getCounterInstance();
        counterInstance.productCount++;
        counterCollection.doc(counterInstance.id).set(counterInstance);
        return counterInstance.productCount;
    },
    decreaseProduct: async function () {
        await getCounterInstance();
        counterInstance.productCount--;
        counterCollection.doc(counterInstance.id).set(counterInstance);
        return counterInstance.productCount;
    }
}


export default {
    db,
    auth,
    currentUser,
    productCollection,
    customerCollection,
    orderCollection,
    counterCollection,
    counterService
}