import Vue from 'vue'
import Router from 'vue-router'
import firebase from 'firebase'

// Containers
const DefaultContainer = () => import('@/containers/DefaultContainer')

// Views
const Dashboard = () => import('@/views/Dashboard')

import ProductsPage from "@/views/pages/products"
import CustomersPage from "@/views/pages/customers"
import SettingsPage from "@/views/pages/settings"
import OrdersPage from "@/views/pages/orders"
import OrderDetailPage from "@/views/pages/orderDetail"
import OrderDetailCollectionPage from "@/views/pages/orderDetailCollection"
import OrderDetailShipmentPage from "@/views/pages/orderDetailShipment"
import OrderStatisticPage from "@/views/pages/orderStatistic"
import UtilsPage from "@/views/pages/utils"
import LoginPage from "@/views/pages/Login"
import ShipmentsPage from "@/views/pages/shipments"

Vue.use(Router)

function configRoutes() {
  return [
    {
      path: '/',
      redirect: '/dashboard',
      name: 'Home',
      component: DefaultContainer,
      meta: {
        requiresAuth: true
      },
      children: [
        {
          path: 'dashboard',
          name: 'Dashboard',
          component: Dashboard,
          meta: {
            title: "Dashboard"
          }
        },
        {
          path: 'products',
          name: 'Products',
          component: ProductsPage,
          meta: {
            title: "Products"
          }
        },
        {
          path: 'customers',
          name: 'Customers',
          component: CustomersPage,
          meta: {
            title: "Customers"
          }
        },
        {
          path: 'orders',
          name: 'Orders',
          component: OrdersPage,
          meta: {
            title: "Orders"
          },
          children: [
            {
              path: ":id/statistic",
              name: "OrderStatistic",
              component: OrderStatisticPage
            }
          ]
        },
        {
          path: 'orders/:id',
          name: 'OrderDetail',
          component: OrderDetailPage,
          meta: {
            title: "Order Detail"
          }
        },
        {
          path: 'orders/:id/collectionpage',
          name: 'OrderDetailCollection',
          component: OrderDetailCollectionPage,
          meta: {
            title: "Order Detail Collection"
          }
        },
        {
          path: 'orders/:id/shipment',
          name: 'OrderDetailShipment',
          component: OrderDetailShipmentPage,
          meta: {
            title: "Order Detail Shipment"
          }
        },
        {
          path: 'shipments',
          name: 'Shipments',
          component: ShipmentsPage,
          meta: {
            title: "Shipments"
          }
        },
        {
          path: 'utils',
          name: 'UtilsPage',
          component: UtilsPage,
          meta: {
            title: "Utils"
          }
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: LoginPage,
      meta: {
        requiresAuth: false,
        title: "Login"
      }
    }
  ]
}

const router = new Router({
  mode: 'hash', // https://router.vuejs.org/api/#mode
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: configRoutes()
});

router.beforeEach((to, from, next) => {
  const currentUser = firebase.auth().currentUser;
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);
  document.title = to.matched.reduce((r, item) => r = item.meta.title ? item.meta.title : "", "");
  if (requiresAuth && !currentUser) next("login");
  else next();

});
export default router;
