<template>
  <b-modal :id="id" title="Product Detail" @ok="saveProduct" size="lg">
    <cw-product-predictive
      :product="prod"
      @productMatched="updateCwMatchedProduct"
      ref="cwProductPredictive"
    ></cw-product-predictive>
    <b-row>
      <b-col sm="8">
        <b-form-group>
          <label for="name">Name</label>
          <b-input-group>
            <b-form-input type="text" placeholder="Enter product name" v-model="prod.name"></b-form-input>
            <b-input-group-append>
              <b-button variant="outline-info" @click="checkCwMatchedProduct">Check</b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
        <b-form-group>
          <label for="name">Thumbnail Url</label>
          <b-form-input type="text" placeholder="Enter product image url" v-model="prod.thumbnail"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <div class="card">
          <img
            :src="prod.thumbnail"
            :alt="prod.name"
            width="150px"
            height="150px"
            style="margin: auto"
          />
        </div>
      </b-col>
    </b-row>

    <b-row>
      <b-col sm="4">
        <b-form-group>
          <label for="weight">Weight (g)</label>
          <b-link class="ml-2" @click="searchShipppingWeight">Search</b-link>
          <b-form-input type="text" id="weight" placeholder="0" v-model="prod.weight"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group>
          <label for="bvprice">BV Price (A$)</label>
          <b-form-input type="text" id="bvprice" placeholder="0.00" v-model="prod.bvPrice"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group>
          <label for="cwprice">CW Price (A$) (PID: {{prod.cwProductId}})</label>
          <b-form-input type="text" id="cwprice" placeholder="0.00" v-model="prod.cwPrice"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col sm="4">
        <b-form-group>
          <label for="shipprice">Shipment Price (A$)</label>
          <b-form-input type="text" id="shipprice" placeholder="0.00" v-model="prod.shipPrice"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group>
          <label>
            <b-form-checkbox
              v-model="prod.bvSalePricePick"
              @change="bvSalePricePickChanged"
            >BV Sale Price (A$)</b-form-checkbox>
          </label>
          <b-form-input type="text" id="bvsaleprice" placeholder="0.00" v-model="prod.bvSalePrice"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col sm="4">
        <b-form-group>
          <label>
            <b-form-checkbox
              v-model="prod.cwSalePricePick"
              @change="cwSalePricePickChanged"
            >CW Sale Price (A$)</b-form-checkbox>
          </label>
          <b-form-input type="text" id="buyprice" placeholder="0.00" v-model="prod.cwSalePrice"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col sm="4"></b-col>
      <b-col sm="4">
        <b-form-group>
          <label for="selprice">Selling Price (A$)</label>
          <b-form-input type="text" id="selprice" placeholder="0.00" v-model="prod.selPrice"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col sm="4">
        <b-form-group>
          <label for="buyprice">Buying Price (A$)</label>
          <b-form-input type="text" id="buyprice" placeholder="0.00" v-model="prod.buyPrice"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col sm="4">
        <b-form-group description="Buy. Price + Ship">
          <label for="oriprice">Ori. Price (A$)</label> -
          <vnd-price :value="prod.oriPrice"></vnd-price>
          <b-form-input type="text" id="oriprice" placeholder="0.00" v-model="prod.oriPrice"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group description="Sel. Price + Commission(10%) + Ship + $2">
          <label for="sugprice">
            Sug. Price (A$) -
            <vnd-price :value="prod.sugPrice"></vnd-price>
          </label>
          <b-form-input placeholder="0.00" v-model="prod.sugPrice"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group>
          <label for="rndprice">
            Rnd. Price (VND) -
            <i style="color: red">{{prod.rndPrice | currency(vndConfig)}}</i>
          </label>
          <b-form-input type="text" placeholder="0.00" v-model="prod.rndPrice"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
  </b-modal>
</template>

<script>
import fb from "firebase";
import appSettings from "@/shared/appSettings";
const settings = appSettings.priceSettings;
import vndPrice from "../shared/vndPrice";
import { generateSubString } from "@/shared/utils";
import cwProductPredictive from "./cwProductPredictive";

export default {
  name: "ProductDetail",
  components: { vndPrice, cwProductPredictive },
  props: ["item", "id"],
  data() {
    return {
      prod: {},
      show: false,
      settings: settings,
      cwMatchedProduct: {},
      cwMatchedProductChange: false,
      isCheckingCwPrice: false
    };
  },
  mounted: function() {},
  watch: {
    item: function(newValue) {
      this.prod = { ...newValue };
      // this.$nextTick(() => {
      //   this.checkCWPrice();
      // });
    },
    "prod.bvPrice": function(newValue) {
      this.calculatePrices();
    },
    "prod.cwPrice": function(newValue) {
      this.calculatePrices();
    },
    "prod.weight": function(newValue) {
      this.calculatePrices();
    },
    "prod.bvSalePrice": function(newValue) {
      this.calculatePrices();
    },
    "prod.cwSalePrice": function(newValue) {
      this.calculatePrices();
    },
    "prod.bvSalePricePick": function(newValue) {
      this.calculatePrices();
    },
    "prod.cwSalePricePick": function(newValue) {
      this.calculatePrices();
    }
  },
  computed: {},
  methods: {
    saveProduct: function() {
      var prod = this.prod;
      prod.nameSearch = generateSubString(prod.name);
      if (!prod.name) return;
      if (prod.id) {
        return this.$fb.productCollection
          .doc(prod.id)
          .set(prod)
          .then(() => {
            this.$emit("productSaved", { ...prod });
            this.$bvModal.hide(this.id);
            return prod;
          });
      } else {
        var newProd = this.$fb.productCollection.doc();
        prod.id = newProd.id;
        return newProd.set(prod).then(() => {
          this.$fb.counterService.increaseProduct();
          this.$emit("productSaved", { ...prod });
          this.$bvModal.hide(this.id);
          return prod;
        });
      }
    },
    calculatePrices: function() {
      var {
        weight,
        bvPrice,
        cwPrice,
        bvSalePrice,
        cwSalePrice,
        bvSalePricePick,
        cwSalePricePick
      } = this.prod;
      this.prod.shipPrice = parseFloat(weight || 0) / 100;

      this.prod.selPrice = Math.max(bvPrice || 0, cwPrice || 0);
      this.prod.buyPrice = Math.min(bvPrice || 9999, cwPrice || 9999);

      this.calculateItself();

      //override if using sale price
      if ((cwSalePrice || 0) > 0 && cwSalePricePick) {
        this.prod.selPrice = cwSalePrice;
        this.prod.buyPrice = cwSalePrice;

        this.calculateItself();
      }
      if ((bvSalePrice || 0) > 0 && bvSalePricePick) {
        this.prod.selPrice = bvSalePrice;
        this.prod.buyPrice = bvSalePrice;

        this.calculateItself();
      }
    },
    calculateItself: function() {
      this.prod.oriPrice = (
        parseFloat(this.prod.shipPrice) + parseFloat(this.prod.buyPrice)
      ).toFixed(2);
      this.prod.sugPrice = parseFloat(
        this.prod.shipPrice + this.prod.selPrice * settings.commissionRate + settings.buyingFee
      ).toFixed(2);
      this.prod.sugPriceVND = parseFloat(this.prod.sugPrice * settings.vndExchageRate).toFixed(2);
    },
    bvSalePricePickChanged: function(checked) {
      if (checked) this.prod.cwSalePricePick = false;
    },
    cwSalePricePickChanged: function(checked) {
      if (checked) this.prod.bvSalePricePick = false;
    },
    updateCwMatchedProduct: function(item) {
      var { thumbnail, id, priceStatus, price, label } = item;

      this.prod.name = label;

      if (!this.prod.thumbnail) this.$set(this.prod, "thumbnail", thumbnail);
      else if (thumbnail != this.prod.thumbnail) {
        this.prod.thumbnail = thumbnail;
      }

      if (!this.prod.cwPrice) this.$set(this.prod, "cwPrice", price);
      else if (priceStatus != "equal") {
        this.prod.cwPrice = parseFloat(price);
      }

      if (!this.prod.cwProductId) this.$set(this.prod, "cwProductId", id);
      else if (this.prod.cwProductId != id) {
        this.prod.cwProductId = id;
      }
    },
    checkCwMatchedProduct: function() {
      this.$refs.cwProductPredictive.checkPrice();
    },
    searchShipppingWeight: function() {
      var url =
        "https://www.google.com.au/search?source=hp&ei=b54BXsjALoiW4-EPl8izwA4&q={term}&oq={term}&gs_l=psy-ab.3...9077.14394..15665...1.0..0.238.3323.0j15j3......0....2j1..gws-wiz.......33i21j33i10.9gohnIlLrZE&ved=0ahUKEwiItLOew83mAhUIyzgGHRfkDOgQ4dUDCAg&uact=5";
      var term = this.prod.name + " " + "shipping weight".replace(" ", "+");
      url = url.replace("{term}", term);
      var win = window.open(url, "_blank");
      win.focus();
    }
  }
};
</script>
