<template>
  <div class="animated fadeIn">
    <b-card>
      <div slot="header">
        <i class="cui-user icons font-lg"></i>
        {{order.name}} Statistic
      </div>
      <b-row>
        <b-col sm="12" md="6">
          <table class="table table-response">
            <tr class="no-border-top">
              <td>Buying</td>
              <td class="text-right">{{totalBuying | currency(audConfig)}}</td>
            </tr>
            <tr>
              <td>
                Ship VN Fee
                <a href="javascript:void(0)" @click="changeShipFee">[Change]</a>
              </td>
              <td
                v-if="!isChangingShipFee"
                class="text-right"
              >{{order.shipFee | currency(audConfig)}}</td>
              <td v-else class="text-right">
                <b-input-group>
                  <b-form-input v-model="order.shipFee"></b-form-input>
                  <b-input-group-append>
                    <b-button variant="outline-success" @click="saveShipFee">Save</b-button>
                  </b-input-group-append>
                </b-input-group>
              </td>
            </tr>
            <tr>
              <td>Total</td>
              <td class="text-right">{{total | currency(audConfig)}}</td>
            </tr>
            <tr>
              <td>
                Current Rate
                <a href="javascript:void(0)" @click="changeCurrentRate">[Change]</a>
              </td>
              <td
                v-if="!isChangingCurrentRate"
                class="text-right"
              >{{order.currentRate | currency(vndConfig)}}</td>
              <td v-if="isChangingCurrentRate" class="text-right">
                <b-input-group>
                  <b-form-input v-model="order.currentRate"></b-form-input>
                  <b-input-group-append>
                    <b-button variant="outline-success" @click="saveCurrentRate">Save</b-button>
                  </b-input-group-append>
                </b-input-group>
              </td>
            </tr>
            <tr>
              <td>Total VND</td>
              <td class="text-right">{{totalVND | currency(vndConfig)}}</td>
            </tr>
            <tr>
              <td>Profit</td>
              <td class="text-right">
                <strong>{{profit | currency(vndConfig)}}</strong>
              </td>
            </tr>
          </table>
        </b-col>
        <b-col sm="12" md="6">
          <b-form-group>
            <label>Note</label>
            <b-form-textarea v-model="order.note" placeholder="Enter something..." rows="6"></b-form-textarea>
          </b-form-group>
          <b-button type="submit" size="sm" variant="success" @click="saveOrder">Save</b-button>
        </b-col>
      </b-row>
    </b-card>
  </div>
</template>

<script>
import fb from "firebase";

export default {
  name: "Orders",
  components: {},
  data() {
    return {
      order: [],
      counter: {},
      totalBuying: 0,
      total: 0,
      totalVND: 0,
      profit: 0,
      isChangingShipFee: false,
      isChangingCurrentRate: false
    };
  },
  mounted: function() {
    this.loadOrder();
  },
  watch: {
    "$route.params.id": function() {
      this.loadOrder();
    }
  },
  computed: {},
  methods: {
    loadOrder: function() {
      var id = this.$route.params.id;
      this.$fb.orderCollection.doc(id).onSnapshot(snapshot => {
        if (!snapshot.empty) {
          this.order = snapshot.data();
          this.calculateStatistic(this.order);
        }
      });
    },
    calculateStatistic: function(order) {
      this.totalBuying = _.sumBy(
        order.items,
        x => parseFloat(x.product.buyPrice || 0) * parseFloat(x.quantity || 0)
      );
      this.total = this.totalBuying + ((order && order.shipFee) || 0);
      this.totalVND = this.total * ((order && order.currentRate) || 16500);
      this.profit = this.sumTotal(order) - this.totalVND;
    },
    sumQuantity: function(order) {
      if (!order.items || order.items.length == 0) return 0;
      return order.items.reduce((sum, x) => (sum += x.quantity), 0);
    },
    sumTotal: function(order) {
      if (!order.items || order.items.length == 0) return 0;
      return order.items.reduce(
        (sum, x) =>
          (sum += x.quantity * parseFloat(x.rndPrice || x.product.rndPrice)),
        0
      );
    },
    changeShipFee: function() {
      this.isChangingShipFee = true;
    },
    changeCurrentRate: function() {
      this.isChangingCurrentRate = true;
    },
    saveShipFee: function() {
      this.order.shipFee = parseFloat(this.order.shipFee);
      this.isChangingShipFee = false;
      this.saveOrder();
    },
    saveCurrentRate: function() {
      this.order.currentRate = parseFloat(this.order.currentRate);
      this.saveOrder();
    },
    saveOrder: function() {
      this.$fb.orderCollection
        .doc(this.order.id)
        .set(this.order)
        .then(x => {
          this.isChangingCurrentRate = false;
        });
    }
  }
};
</script>
<style scoped>
.no-border-top td {
  border-top: none;
}
</style>