<template>
  <b-modal :id="id" title="Customer Detail" @ok="saveCustomer" size="lg">
    <b-row>
      <b-col sm="12">
        <b-form-group>
          <label>Name</label>
          <b-form-input type="text" v-model="customer.name"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="12">
        <b-form-group>
          <label>Address</label>
          <b-form-input type="text" v-model="customer.address"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="12">
        <b-form-group>
          <label>Phone</label>
          <b-form-input type="text" v-model="customer.phone"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
  </b-modal>
</template>

<script>
import { generateSubString } from "@/shared/utils";
export default {
  name: "CustomerEdit",
  props: ["id", "item"],
  components: {},
  data() {
    return {
      customer: {}
    };
  },
  watch: {
    item: function(newValue) {
      this.customer = newValue || {};
      if (this.customer.id) {
        this.$fb.customerCollection
          .doc(this.customer.id)
          .get()
          .then(snapshot => (this.customer = snapshot.data()));
      }
    }
  },
  mounted: function() {},
  computed: {},
  methods: {
    saveCustomer: function() {
      this.customer.nameSearch = generateSubString(this.customer.name);
      this.customer.version = (this.customer.version || 0) + 1;
      this.$fb.customerCollection
        .doc(this.customer.id)
        .set(this.customer)
        .then(() => {
          this.$emit("customerSaved", this.customer);
          this.$bvModal.hide(this.id);
        });
    }
  }
};
</script>
