<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <b-link @click="goToOrderList">
              <i class="fa fa-server"></i> Order:
            </b-link>
            <strong>{{order.name}}</strong>
            <div class="pull-right">
              <b-link @click="updateCustomerInformation" class="mr-3">Update Customer Information</b-link>
              <b-link @click="goToOrderDetailPage" class="mr-3">Order Detail Page</b-link>
            </div>
          </div>

          <b-table
            foot-clone
            responsive="sm"
            :items="items"
            :fields="fields"
            :small="true"
            :hover="true"
          >
            <template v-slot:cell(index)="data">{{data.index +1}}</template>
            <template v-slot:cell(customerName)="data">
              <b-link @click="editCustomer(data)">{{data.value}}</b-link>
              <b-link
                v-if="!data.item.customerId"
                class="ml-1"
                @click="updateCustomer(data.item)"
              >[Update Info]</b-link>
            </template>
            <template v-slot:cell(products)="data">
              <table class="table table-borderless product-table">
                <tr v-for="(item, index) in data.item.products" :key="index">
                  <td>{{item.productName}}</td>
                  <td class="text-right" style="width: 5%">{{item.quantity}}</td>
                  <td class="text-right" style="width: 25%">{{item.total |currency(vndConfig)}}</td>
                </tr>
              </table>
            </template>
            <template v-slot:cell(productPrice)="data">
              <div v-for="(item, index) in data.item.products" :key="index">
                <span>{{item.total | currency(vndConfig)}}</span>
              </div>
            </template>

            <template v-slot:cell(quantity)="data">
              <strong>{{data.value}}</strong>
            </template>
            <template v-slot:cell(total)="data">
              <strong>{{data.value | currency(vndConfig)}}</strong>
            </template>
            <template v-slot:cell(purchased)="data">
              <b-form-checkbox
                v-model="data.item.purchased"
                @change="purchasedChanged($event, data)"
              ></b-form-checkbox>
            </template>
            <template v-slot:cell(action)="data">
              <b-button type="submit" size="sm" variant="success" @click="editShipmentItem(data)">
                <i class="fa fa-edit"></i> Edit
              </b-button>
            </template>
            <!-- footer -->
            <template v-slot:foot()="data"></template>
            <template v-slot:foot(quantity)="data">{{sumQuantity}}</template>
            <template v-slot:foot(total)="data">{{sumTotal | currency(vndConfig)}}</template>
            <template v-slot:foot(purchased)="data">{{sumPurchased | currency(vndConfig)}}</template>
          </b-table>
        </b-card>
      </b-col>
    </b-row>
    <!--/.row-->

    <order-detail-shipment-edit-item
      id="shipmentEditItem"
      :item="selectedShipmentItem"
      @saveShipmentItem="saveShipmentItem"
    ></order-detail-shipment-edit-item>

    <customer-edit id="customerEdit" :item="this.selectedCustomer"></customer-edit>
  </div>
</template>

<script>
import _ from "lodash";
import numberInput from "../shared/numberInput";
import orderDetailShipmentEditItem from "./orderDetailShipmentEditItem";
import customerEdit from "./customerEdit";

var saveOrderTimeoutFunc;
export default {
  name: "OrderDetail",
  components: { numberInput, orderDetailShipmentEditItem, customerEdit },
  data() {
    return {
      order: {},
      items: [],
      selectedShipmentItem: {},
      selectedCustomer: {},
      fields: [
        { key: "index", label: "#", class: "shipment-index-col" },
        {
          key: "customerName",
          label: "Customer",
          class: "shipment-customer-col"
        },
        { key: "products", label: "Products", class: "shipment-product-col" },
        // {
        //   key: "productPrice",
        //   label: "",
        //   class: "text-center shipment-product-price"
        // },
        {
          key: "quantity",
          label: "Total Qty",
          class: "text-center shipment-quantity-col",
          tdClass: "vertical-middle"
        },
        {
          key: "total",
          label: "Total Price",
          class: "text-right shipment-price-col"
        },
        {
          key: "purchased",
          label: "Purchased",
          class: "text-center shipment-purchased-col"
        },
        {
          key: "note",
          label: "Note",
          class: "shipment-note-col"
        },
        { key: "address", label: "Address", class: "shipment-address-col" },
        { key: "phone", label: "Phone", class: "shipment-phone-col" },
        {
          key: "action",
          label: "Action",
          class: "text-right shipment-action-col"
        }
      ]
    };
  },
  mounted: function() {
    this.loadOrder();
  },
  computed: {
    sumQuantity: function() {
      if (!this.items || this.items.length === 0) return 0;
      return _.sumBy(this.items, x => x.quantity);
    },
    sumTotal: function() {
      if (!this.items || this.items.length === 0) return 0;
      return _.sumBy(this.items, x => x.total);
    },
    sumPurchased: function() {
      if (!this.items || this.items.length === 0) return 0;
      return _.chain(this.items)
        .filter(x => x.purchased)
        .sumBy(x => x.total)
        .value();
    }
  },
  methods: {
    loadOrder: function() {
      this.$fb.orderCollection
        .doc(this.$route.params.id)
        .onSnapshot(snapshot => {
          if (!snapshot.empty) {
            this.order = snapshot.data();
            this.buildItems(this.order);
          }
        });
    },
    buildItems: function(order) {
      this.items = _.chain(order.items)
        .map(x => {
          return {
            id: x.id,
            customerId: x.customer.id,
            note: x.note,
            customerName: x.customer.name,
            address: x.customer.address,
            phone: x.customer.phone,
            purchased: x.purchased,
            productName: x.product.name,
            quantity: x.quantity,
            rndPrice: x.rndPrice || x.product.rndPrice,
            total:
              parseFloat(x.quantity || 0) *
              parseFloat(x.rndPrice || x.product.rndPrice || 0)
          };
        })
        .groupBy(x => x.customerName)
        .mapValues(val => {
          var re = {
            note: _.first(val).note,
            customerId: _.first(val).customerId,
            customerName: _.first(val).customerName,
            address: _.first(val).address,
            phone: _.first(val).phone,
            purchased: _.first(val).purchased,
            quantity: _.sumBy(val, x => x.quantity),
            total: _.sumBy(val, x => x.total),
            products: _.reduce(val, (s, x) => s.concat(x), [])
          };
          return re;
        })
        .values()
        .sortBy(x => x.customerName)
        .value();
    },

    goToOrderDetailPage: function() {
      this.$router.push({ name: "OrderDetail", params: { id: this.order.id } });
    },
    collectedChanged: function(checked, item) {
      if (checked) {
        item.collectedQuantity = item.quantity;
      } else {
        item.collectedQuantity = 0;
      }
      this.setCollectedItem(item);
    },
    setCollectedItem: function(item) {
      var { orderItemIds } = item;
      var collectedQuantity = item.collectedQuantity;
      this.order.items
        .filter(x => orderItemIds.indexOf(x.id) > -1)
        .forEach(x => {
          x.collectedQuantity = 0;
          x.collected = false;
          if (collectedQuantity > 0) {
            if (collectedQuantity > x.quantity) {
              x.collectedQuantity = x.quantity;
              x.collected = true;
              collectedQuantity -= x.quantity;
            } else {
              x.collectedQuantity = collectedQuantity;
              collectedQuantity = 0;
              x.collected = x.collectedQuantity == x.quantity;
            }
          }
        });

      this.debounceSaveOrder();
    },
    goToOrderList: function() {
      this.$router.push({
        name: "Orders"
      });
    },
    collectedQuantityChanged: function(item, qty) {
      item.collectedQuantity = qty;
      this.setCollectedItem(item);
    },
    debounceSaveOrder: function() {
      if (saveOrderTimeoutFunc) window.clearTimeout(saveOrderTimeoutFunc);

      saveOrderTimeoutFunc = window.setTimeout(
        () => this.$fb.orderCollection.doc(this.order.id).set(this.order),
        500
      );
    },
    updateCustomerInformation: function() {
      this.$bvModal
        .msgBoxConfirm(
          `Are you sure you want to update all Customer's informations?`
        )
        .then(value => {
          if (value) {
            this.items.forEach(this.updateCustomer);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    updateCustomer: function(item) {
      var query = this.$fb.customerCollection;
      if (item.customerId) {
        query = query.doc(item.customerId);
      } else {
        query = query.where("name", "==", item.customerName);
      }

      query
        .get()
        .then(snapshot => {
          var newCus;
          if (item.customerId) {
            newCus = snapshot.data();
          } else {
            newCus = _.first(snapshot.docs).data();
          }

          delete newCus.nameSearch;
          return newCus;
        })
        .then(newCus => {
          var indices = _.keys(
            _.pickBy(this.order.items, item => {
              return (
                item.customer.id == newCus.id ||
                item.customer.name == newCus.name
              );
            })
          );
          indices.forEach(index => {
            var item = this.order.items[index];
            item.customer = newCus;
            this.order.items.splice(index, 1, item);
          });

          this.$fb.orderCollection.doc(this.order.id).set(this.order);
          return this.order;
        })
        .then(this.buildItems);
    },
    purchasedChanged: function(checked, data) {
      var { index, item } = data;
      item.purchased = checked;
      this.items.splice(index, 1, item);

      var indices = _.keys(
        _.pickBy(this.order.items, x => {
          return (
            x.customer.id == item.customerId ||
            x.customer.name == item.customerName
          );
        })
      );
      indices.forEach(index => (this.order.items[index].purchased = checked));

      this.debounceSaveOrder();
    },
    editShipmentItem: function(data) {
      var { index, item } = data;
      this.selectedShipmentItem = { ...item };
      this.selectedShipmentItemIndex = index;
      this.$bvModal.show("shipmentEditItem");
    },
    saveShipmentItem: function(item) {
      console.log(item);
      this.items.splice(this.selectedShipmentItemIndex, 1, item);

      var indices = _.keys(
        _.pickBy(this.order.items, x => {
          return (
            x.customer.id == item.customerId ||
            x.customer.name == item.customerName
          );
        })
      );
      indices.forEach(index => {
        this.order.items[index].note = item.note;
        this.order.items[index].customer.address = item.address;
        this.order.items[index].customer.phone = item.phone;
      });
      this.debounceSaveOrder();
    },
    editCustomer: function(data) {
      var { index, item } = data;
      var { customerId, customerName, address, phone } = item;
      this.selectedCustomer = {
        id: customerId,
        name: customerName,
        address: address,
        phone: phone
      };
      this.$bvModal.show("customerEdit");
    }
  }
};
</script>
<style scoped>
.product-display {
  display: flex;
  justify-content: space-between;
}

.product-table {
  margin-bottom: 0;
}
.product-table tr:hover {
  background-color: inherit;
}
</style>