<template>
  <div class="animated fadeIn">
    <!-- Product batches -->
    <!-- <b-row>
      <b-col>
        <textarea v-model="productBatches" cols="30" rows="10" class="form-control"></textarea>
        <b-button type="submit" size="sm" variant="success" @click="saveProductBatches">
          <i class="fa fa-save"></i> Save
        </b-button>
      </b-col>
    </b-row>-->
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <span>
              <i class="fa fa-book"></i>
              Products ({{productCount}})
            </span>
            <b-link class="pull-right" @click="addNewProduct">Add New Product</b-link>
          </div>
          <b-row>
            <b-col sm="12" md="6">
              <b-input-group class="mb-5">
                <b-form-input v-model="searchText" @keydown="seartTextKeydown($event)"></b-form-input>

                <b-input-group-append>
                  <span
                    id="searchclear"
                    class="fa fa-close"
                    v-if="searchText != ''"
                    @click="clearSearchText"
                  ></span>
                  <b-button variant="outline-success" @click="clickSearchProduct()">
                    <span class="fa fa-search"></span> Search
                  </b-button>
                </b-input-group-append>
              </b-input-group>
            </b-col>
          </b-row>
          <products-list
            :products="products"
            :search-text="searchText"
            @productClicked="productClicked"
            @deleteProduct="deleteProduct"
            @editProduct="editProduct"
            v-if="!useMobileView"
          ></products-list>

          <products-list-mobile
            :products="products"
            @productClicked="productClicked"
            @deleteProduct="deleteProduct"
            @editProduct="editProduct"
            v-if="useMobileView"
          ></products-list-mobile>

          <b-row class="load-more">
            <b-button
              variant="outline-success"
              @click="loadMoreProduct($event)"
              :disabled="isLoadingMore"
            >Load more</b-button>
          </b-row>
        </b-card>
      </b-col>
    </b-row>
    <!--/.row-->

    <product-detail id="productEdit" :item="product" @productSaved="productSaved" />
  </div>
</template>

<script>
import fb from "firebase";
import productDetail from "./productDetail";
import productsList from "./productsList";
import productsListMobile from "./productsListMobile";
import { generateSubString } from "@/shared/utils";

export default {
  name: "Products",
  components: {
    productDetail,
    productsList,
    productsListMobile
  },
  data() {
    return {
      products: [],
      product: {},
      productBatches: "",
      useMobileView: false,
      searchText: "",
      pageSize: 10,
      lastVisible: undefined,
      productCount: 0,
      isLoadingMore: false
    };
  },
  mounted: function() {
    var self = this;
    self.useMobileView = window.innerWidth < 700;
    window.addEventListener("resize", function() {
      self.useMobileView = window.innerWidth < 700;
    });
    this.loadProducts();
    this.$fb.counterService
      .getProductCount()
      .then(count => (this.productCount = count));
  },
  watch: {
    searchText: function(newValue, oldValue) {
      if (oldValue != "" && newValue == "") {
        this.products = [];
        this.loadProducts();
      }
    }
  },
  computed: {},
  methods: {
    loadProducts: function(lastVisible) {
      return this.$fb.productCollection
        .orderBy("name")
        .startAfter(this.lastVisible || 0)
        .limit(this.pageSize)
        .get()
        .then(snapshot => {
          if (!snapshot.empty) {
            this.lastVisible = _.last(snapshot.docs);
            snapshot.forEach(doc => {
              this.products.push(doc.data());
            });
          }
        });
    },
    loadMoreProduct: function(e) {
      this.isLoadingMore = true;
      if (this.searchText) {
        this.searchProduct(this.lastVisible).then(
          () => (this.isLoadingMore = false)
        );
      } else {
        this.loadProducts(this.lastVisible).then(
          () => (this.isLoadingMore = false)
        );
      }
    },
    productClicked: function(product) {
      this.product = { ...product };
    },
    deleteProduct: function(data) {
      var product = data.item;
      var index = data.index;

      if (!product) return;

      this.$bvModal
        .msgBoxConfirm(`Are you sure you want to delete [${product.name}]?`)
        .then(value => {
          if (value) {
            this.$fb.productCollection
              .doc(product.id)
              .delete()
              .then(() => {
                this.products.splice(index, 1);
              });
            this.$fb.counterService
              .decreaseProduct()
              .then(x => (this.productCount = x));
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    addNewProduct: function() {
      this.product = undefined;
      this.selectedIndex = undefined;
      this.$bvModal.show("productEdit");
    },
    productSaved: function(prod) {
      if (this.selectedIndex != undefined) {
        //edit
        this.products.splice(this.selectedIndex, 1, prod);
      } else {
        this.products.push(prod);
      }
    },
    editProduct: function(data) {
      var { index, item } = data;
      this.product = { ...item };
      this.selectedIndex = index;
      this.$bvModal.show("productEdit");
    },
    saveProductBatches: function() {
      var productBatches = JSON.parse(this.productBatches);

      productBatches.forEach(prod => {
        prod.nameSearch = generateSubString(prod.name);
      });

      console.table(productBatches);
      // Get a new write batch
      var batch = this.$fb.db.batch();

      productBatches.forEach(prod => {
        var newProd = this.$fb.productCollection.doc();
        prod.id = newProd.id;
        batch.set(newProd, prod);
      });

      // Commit the batch
      batch.commit().then(function() {
        console.log("Finish Product Batches");
      });
    },
    clickSearchProduct: function() {
      this.products = [];
      this.searchProduct();
    },
    searchProduct: function(lastVisible) {
      if (!this.searchText) return;

      return this.$fb.productCollection
        .where(
          "nameSearch",
          "array-contains",
          this.searchText.toLowerCase().trim()
        )
        .orderBy("name")
        .startAfter(lastVisible || 0)
        .limit(10)
        .get()
        .then(snapshot => {
          if (!snapshot.empty) {
            this.lastVisible = _.last(snapshot.docs);
            snapshot.forEach(doc => {
              this.products.push(doc.data());
            });
          }
        });
    },
    seartTextKeydown: function(e) {
      if (e.keyCode === 13) {
        this.products = [];
        this.searchProduct();
      }
    },
    clearSearchText: function() {
      this.searchText = "";
    }
  }
};
</script>
<style>
.label {
  font-weight: bold;
}

.name {
  width: 20%;
}
.weight {
  width: 10%;
}
.temp-price {
  width: 40%;
}
.rnd-price {
  width: 20%;
}
.rnd-price .price {
  font-size: 18px;
  font-weight: bold;
  margin-right: 5px;
}

.action {
  width: 10%;
}

#searchclear {
  position: absolute;
  right: 90px;
  top: 0;
  bottom: 0;
  height: 14px;
  margin: auto;
  font-size: 14px;
  cursor: pointer;
  color: #ccc;
  z-index: 1019;
}

.load-more {
  text-align: center;
  display: block;
}
</style>
