<template>
  <b-row>
    <b-col sm="12">
      <div class="alert alert-primary" role="alert" style="padding: 5px">
        <template v-if="isChecking">
          <div class="spinner-border spinner-border-sm text-success" role="status">
            <span class="sr-only"></span>
          </div>&nbsp;&nbsp;Checking CW Price...
        </template>
        <template v-if="!isChecking">
          <template v-if="matchedProducts.length == 0">Not found</template>
          <template v-else>
            <table class="table table-borderless">
              <tr v-for="(item, index) in matchedProducts" :key="index">
                <td style="width: 10%">#{{item.id}}</td>
                <td>{{item.label}}</td>
                <td class="text-center">
                  {{item.price | currency(audConfig)}}
                  <i
                    class="fa fa-angle-double-up fa-lg"
                    aria-hidden="true"
                    v-if="item.priceStatus == 'greater'"
                    style="color: red"
                  ></i>
                  <i
                    class="fa fa-angle-double-down fa-lg"
                    aria-hidden="true"
                    v-if="item.priceStatus == 'less'"
                    style="color: green"
                  ></i>
                </td>
                <td class="text-right">
                  <b-button type="submit" size="sm" variant="success" @click="matchProduct(item)">
                    <i class="fa fa-check"></i> Select
                  </b-button>
                </td>
              </tr>
            </table>
          </template>
        </template>
      </div>
    </b-col>
  </b-row>
</template>

<script>
export default {
  name: "cwProductPredictive",
  components: {},
  props: ["product"],
  data() {
    return {
      matchedProducts: [],
      isChecking: false,
      hasChanged: false
    };
  },
  mounted: function() {
    this.checkPrice();
  },
  watch: {},
  computed: {},
  methods: {
    checkPrice: function() {
      this.matchedProducts = [];
      if (!this.product || !this.product.name) return;

      var { cwProductId, name } = this.product;

      var term = cwProductId || name;
      this.isChecking = true;
      this.hasChanged = false;
      this.$http.get(this.appSettings.cwCheckPriceApi.replace("{term}", term)).then(res => {
        if (res && res.status == 200 && res.data) {
          var data = res.data;
          var validData = _.filter(data, item => parseFloat(item.id) > 0);
          if (validData) {
            validData = _.map(validData, this.decorateData);
            validData.forEach(item => this.matchedProducts.push(item));
          }

          this.hasChanged = _.some(this.matchedProducts, { hasChanged: true });
        }
        this.isChecking = false;
      });
    },
    decorateData: function(item) {
      item.price = parseFloat(item.Price);
      item.thumbnail = this.appSettings.cwProductThumbnailUrl150.replace("{id}", item.id);
      var price = item.price;
      var cwPrice = parseFloat(this.product.cwPrice);

      if (price > cwPrice + 0.5) {
        item.priceStatus = "greater";
      } else if (price < cwPrice - 0.5) {
        item.priceStatus = "less";
      } else {
        item.priceStatus = "equal";
      }

      item.hasChanged = item.thumbnail != this.product.thumbnail || item.priceStatus != "equal";
      return item;
    },
    matchProduct: function(item) {
      this.$emit("productMatched", { ...item });
    }
  }
};
</script>
