<template>
  <b-table
    ref="productTable"
    responsive="sm"
    :items="items"
    :fields="fields"
    :small="true"
    selectable
    select-mode="single"
    @row-clicked="productClicked"
    @row-dblclicked="productDoubleClicked"
  >
    <template v-slot:cell(index)="data">{{data.index + 1}}</template>
    <template v-slot:cell(name)="data">
      <div class="prod-thumbnail-name">
        <div class="thumbnail">
          <img :src="data.item.thumbnail" width="50px" height="50px" v-if="data.item.thumbnail" />
        </div>
        <div class="name-2">
          <text-highlight :queries="queries">{{data.value}}</text-highlight>
          <br />
          <small>PID: {{data.item.cwProductId}}</small>
        </div>
      </div>
    </template>
    <template v-slot:cell(weight)="data">
      <strong>{{data.item.weight}}(g)</strong>
      - {{data.item.shipPrice | currency(audConfig)}}
    </template>
    <template v-slot:cell(tempPrices)="data">
      <div class="row">
        <div class="col-md-3 col-sm-6 label">BV Price</div>
        <div class="col-md-3 col-sm-6">{{data.item.bvPrice | currency(audConfig)}}</div>
        <div class="col-md-3 col-sm-6 label">CW Price</div>
        <div class="col-md-3 col-sm-6">{{data.item.cwPrice | currency(audConfig)}}</div>
      </div>
      <div class="row">
        <div class="col-md-3 col-sm-6 label">BV Sale Price</div>
        <div class="col-md-3 col-sm-6">{{data.item.bvSalePrice | currency(audConfig)}}</div>
        <div class="col-md-3 col-sm-6 label">CW Sale Price</div>
        <div class="col-md-3 col-sm-6">{{data.item.cwSalePrice | currency(audConfig)}}</div>
      </div>
    </template>
    <template v-slot:cell(rndPrice)="data">
      <span class="price">{{data.item.rndPrice | currency(vndConfig)}}</span>
      <b-badge v-if="data.item.bvSalePricePick" variant="success">BV Sale</b-badge>
      <b-badge v-if="data.item.cwSalePricePick" variant="success">CW Sale</b-badge>
      <b-badge v-if="(data.item.sugPrice * 16500) >data.item.rndPrice " variant="warning">Recheck</b-badge>
      <br />
      <small>Sug. Price: {{data.item.sugPrice | currency(audConfig)}} - {{data.item.sugPrice * 16500 | currency(vndConfig)}}</small>
    </template>
    <template v-slot:cell(action)="data">
      <b-button type="submit" size="sm" variant="success" @click="editProduct(data)" class="mr-1">
        <i class="fa fa-edit"></i> Edit
      </b-button>
      <b-button type="submit" size="sm" variant="danger" @click="deleteProduct(data)">
        <i class="fa fa-remove"></i> Delete
      </b-button>
    </template>
  </b-table>
</template>

<script>
import TextHighlight from "vue-text-highlight";
export default {
  name: "ProductsListMobile",
  components: { TextHighlight },
  props: ["products", "searchText"],
  data() {
    return {
      fields: [
        { key: "index", label: "#" },
        { key: "name", class: "name" },
        { key: "weight", label: "Weight (g)", class: "weight" },
        { key: "tempPrices", label: "Temp. Prices", class: "temp-price" },
        { key: "rndPrice", label: "Price", class: "rnd-price" },
        { key: "action", label: "Action", class: "action" }
      ]
    };
  },
  mounted: function() {},
  computed: {
    items: function() {
      return this.products || [];
    },
    queries: function() {
      return [this.searchText];
    }
  },
  methods: {
    productClicked: function(prod) {
      this.$emit("productClicked", prod);
    },
    deleteProduct: function(data) {
      this.$emit("deleteProduct", data);
    },
    editProduct: function(data) {
      this.$emit("editProduct", data);
      this.$refs.productTable.selectRow(data.index);
    },
    productDoubleClicked: function(item, index) {
      var data = { item: item, index: index };
      this.editProduct(data);
    }
  }
};
</script>
<style>
.prod-thumbnail-name {
  display: flex;
  justify-content: flex-start;
}
.prod-thumbnail-name .name-2 {
  margin-left: 5px;
}
.prod-thumbnail-name .thumbnail {
  width: 50px;
  min-width: 50px;
  height: 50px;
  min-height: 50px;
}
.label {
  font-weight: bold;
}

.name {
  width: 20%;
}
.weight {
  width: 10%;
}
.temp-price {
  width: 40%;
}
.rnd-price {
  width: 20%;
}
.rnd-price .price {
  font-size: 18px;
  font-weight: bold;
  margin-right: 5px;
}

.action {
  width: 10%;
}
</style>
