<template>
  <b-table
    responsive="sm"
    :items="items"
    :fields="fields"
    :small="true"
    @row-clicked="productClicked"
  >
    <template v-slot:cell(name)="data">
      <div class="row">
        <div class="col-4" style="overflow: hidden">
          <img :src="data.item.thumbnail" :alt="data.item.name" width="150px" height="150px" />
        </div>
        <div class="col-8">
          <strong>{{data.item.name}}</strong>
          <br />
          <strong>Weight:</strong>
          {{data.item.weight}}(g)
          - {{data.item.shipPrice | currency(audConfig)}}
          <br />
          <strong style="width: 100px;">BV Price</strong>
          : {{data.item.bvPrice | currency(audConfig)}} -
          <strong>Sale</strong>
          : {{data.item.bvSalePrice | currency(audConfig)}}
          <br />
          <strong>CW Price</strong>
          : {{data.item.cwPrice | currency(audConfig)}} -
          <strong>Sale</strong>
          : {{data.item.cwSalePrice | currency(audConfig)}}
          <br />

          <span class="h4">
            <strong>Price</strong>
            : {{data.item.rndPrice | currency(vndConfig)}}
          </span>
          <b-badge v-if="data.item.bvSalePricePick" variant="success">BV Sale</b-badge>
          <b-badge v-if="data.item.cwSalePricePick" variant="success">CW Sale</b-badge>

          <div style="margin-top: 7px">
            <b-button
              type="submit"
              size="sm"
              variant="success"
              @click="editProduct(data)"
              class="mr-1"
            >
              <i class="fa fa-edit"></i> Edit
            </b-button>
            <b-button type="submit" size="sm" variant="danger" @click="deleteProduct(data)">
              <i class="fa fa-remove"></i> Delete
            </b-button>
          </div>
        </div>
      </div>
    </template>
    <template v-slot:cell(tempPrices)="data">
      <div class="row">
        <div class="col-md-3 col-sm-6 label">BV Price</div>
        <div class="col-md-3 col-sm-6">{{data.item.bvPrice | currency(audConfig)}}</div>
        <div class="col-md-3 col-sm-6 label">CW Price</div>
        <div class="col-md-3 col-sm-6">{{data.item.cwPrice | currency(audConfig)}}</div>
      </div>
      <div class="row">
        <div class="col-md-3 col-sm-6 label">BV Sale Price</div>
        <div class="col-md-3 col-sm-6">{{data.item.bvSalePrice | currency(audConfig)}}</div>
        <div class="col-md-3 col-sm-6 label">CW Sale Price</div>
        <div class="col-md-3 col-sm-6">{{data.item.cwSalePrice | currency(audConfig)}}</div>
      </div>
    </template>
    <template v-slot:cell(rndPrice)="data">
      <span class="price">{{data.item.rndPrice | currency(vndConfig)}}</span>
      <b-badge v-if="data.item.bvSalePricePick" variant="success">BV Sale</b-badge>
      <b-badge v-if="data.item.cwSalePricePick" variant="success">CW Sale</b-badge>
    </template>
  </b-table>
</template>

<script>
export default {
  name: "ProductsList",
  components: {},
  props: ["products"],
  data() {
    return {
      fields: [{ key: "name", label: "", class: "" }]
    };
  },
  mounted: function() {},
  computed: {
    items: function() {
      return this.products || [];
    }
  },
  methods: {
    productClicked: function(prod) {
      this.$emit("productClicked", prod);
    },
    deleteProduct: function(data) {
      this.$emit("deleteProduct", data);
    },
    editProduct: function(data) {
      this.$emit("editProduct", data);
    }
  }
};
</script>
<style>
.label {
  font-weight: bold;
}

.name {
  width: 20%;
}
.weight {
  width: 10%;
}
.temp-price {
  width: 40%;
}
.rnd-price {
  width: 20%;
}
.rnd-price .price {
  font-size: 18px;
  font-weight: bold;
  margin-right: 5px;
}

.action {
  width: 10%;
}
</style>
