<template>
  <div>
    <div>{{item.name}} - {{item.phone || 'Not Provided'}}</div>
    <span>{{address}}</span>
  </div>
</template>

<script>
export default {
  props: {
    item: { required: true }
  },
  computed: {
    address: function() {
      return (this.item.address || "Not Provided").substring(0, 100);
    }
  }
};
</script>