<template>
  <div>
    <b-row>
      <b-col sm="3" style="overflow: hidden">
        <img :src="item.thumbnail" :alt="item.name.substring(0,20)" width="50px" height="50px" />
      </b-col>
      <b-col sm="8">
        {{item.name}}
        <br />
        <span class="h5">{{item.rndPrice | currency(vndConfig)}}</span>
      </b-col>
    </b-row>
  </div>
</template>

<script>
export default {
  props: {
    item: { required: true }
  },
  computed: {
    address: function() {
      return (this.item.address || "Not Provided").substring(0, 100);
    }
  }
};
</script>