<template>
  <span style="color: red">
    <i>{{display | currency(vndConfig)}}</i>
  </span>
</template>
<script>
import appSettings from "@/shared/appSettings";
const priceSettings = appSettings.priceSettings;
export default {
  name: "vndPrice",
  props: ["value"],
  mounted: function() {},
  computed: {
    display: function() {
      return parseFloat(this.value) * parseFloat(priceSettings.vndExchageRate);
    }
  }
};
</script>