<template>
  <b-modal :id="id" :title="modalTitle" @ok="saveOrderItem" size="lg">
    <b-row>
      <b-col sm="4">
        <b-form-group>
          <label>Customer</label>
          <vue-suggestion
            :items="sugCustomers"
            v-model="orderItem.customer"
            :item-template="customerSugTemplate"
            :setLabel="setLabel"
            @focus="sugCustomersFocused"
            @onInputChange="sugCustomersInputChanged"
            @scrollEnd="loadMoreSugCustomers"
          ></vue-suggestion>
        </b-form-group>
      </b-col>
      <b-col sm="8">
        <b-form-group>
          <label>Product</label>
          <vue-suggestion
            :items="sugProducts"
            v-model="orderItem.product"
            :item-template="productSugTemplate"
            :setLabel="setLabel"
            @focus="sugProductsFocused"
            @onInputChange="sugProductsInputChanged"
            @onItemSelected="sugProductsItemSelected"
            @scrollEnd="loadMoreSugProducts"
          ></vue-suggestion>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col sm="4">
        <b-form-group>
          <label>Quantity</label>
          <number-input v-model="orderItem.quantity" :min="1" :max="999" inline controls center></number-input>
        </b-form-group>
      </b-col>
      <b-col sm="8">
        <b-form-group>
          <label>Price - {{orderItem.rndPrice | currency(vndConfig)}}</label>
          <b-form-input type="text" placeholder="Enter rnd price" v-model="orderItem.rndPrice"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
  </b-modal>
</template>

<script>
import vueSuggestion from "../shared/vueSuggestion";
import customerSugTemplate from "./customerSugTemplate";
import productSugTemplate from "./productSugTemplate";
import numberInput from "../shared/numberInput";
var loadProductTimeout, loadCustomerTimeout;
export default {
  name: "OrderDetailEdit",
  props: ["id", "item"],
  components: { vueSuggestion, numberInput },
  data() {
    return {
      orderItem: {},
      sugCustomers: [],
      sugProducts: [],
      modalTitle: "",
      customerSugTemplate: customerSugTemplate,
      productSugTemplate: productSugTemplate
    };
  },
  mounted: function() {},
  watch: {
    item: function(newValue) {
      this.orderItem = newValue || {};
      this.modalTitle = !this.orderItem.customer.name
        ? "Add Order Item"
        : "Edit Order Item";
    }
  },
  computed: {},
  methods: {
    loadCustomers: function(searchText, lastVisible) {
      var query = this.$fb.customerCollection.limit(5).orderBy("name");
      if (searchText)
        query = query.where(
          "nameSearch",
          "array-contains",
          searchText.toLowerCase().trim()
        );
      if (lastVisible) query = query.startAfter(lastVisible);

      return query.get().then(snapshot => {
        var customers = [];
        if (!snapshot.empty) {
          this.lastSugCustomerVisible = _.last(snapshot.docs);

          snapshot.forEach(doc => {
            customers.push(doc.data());
          });
        }

        return customers;
      });
    },
    sugCustomersFocused: function() {
      if (this.sugCustomers.length == 0) {
        this.loadCustomers().then(customers =>
          customers.forEach(cus => this.sugCustomers.push(cus))
        );
      }
    },
    sugCustomersInputChanged: function(searchText) {
      this.customerSugSearchText = searchText;
      this.sugCustomers = [];
      this.lastSugCustomerVisible = undefined;

      if (loadCustomerTimeout) window.clearTimeout(loadCustomerTimeout);

      loadCustomerTimeout = this.loadCustomers(searchText).then(customers => {
        customers.forEach(item => this.sugCustomers.push(item));
      });
    },
    loadMoreSugCustomers: function(itemCount) {
      if (this.isLoadingMoreSugCustomer) return;

      this.isLoadingMoreSugCustomer = true;
      this.loadCustomers(
        this.customerSugSearchText,
        this.lastSugCustomerVisible
      ).then(customers => {
        customers.forEach(item => this.sugCustomers.push(item));
        this.isLoadingMoreSugCustomer = false;
      });
    },
    sugProductsFocused: function() {
      if (this.sugProducts.length == 0) {
        //load first 5 customers
        this.loadProducts().then(products =>
          products.forEach(item => this.sugProducts.push(item))
        );
      }
    },
    sugProductsInputChanged: function(searchText) {
      this.productSugSearchText = searchText;
      this.sugProducts = [];
      this.lastSugProductVisible = undefined;

      if (loadProductTimeout) window.clearTimeout(loadProductTimeout);

      loadProductTimeout = this.loadProducts(searchText).then(products => {
        products.forEach(item => this.sugProducts.push(item));
      });
    },
    loadMoreSugProducts: function(itemCount) {
      if (this.isLoadingMoreSugProduct) return;

      this.isLoadingMoreSugProduct = true;
      this.loadProducts(
        this.productSugSearchText,
        this.lastSugProductVisible
      ).then(products => {
        products.forEach(item => this.sugProducts.push(item));
        this.isLoadingMoreSugProduct = false;
      });
    },
    loadProducts: function(searchText, lastVisible) {
      var query = this.$fb.productCollection.limit(5).orderBy("name");
      if (searchText)
        query = query.where(
          "nameSearch",
          "array-contains",
          searchText.toLowerCase().trim()
        );
      if (lastVisible) query = query.startAfter(lastVisible);

      return query.get().then(snapshot => {
        var products = [];
        if (!snapshot.empty) {
          this.lastSugProductVisible = _.last(snapshot.docs);

          snapshot.forEach(doc => {
            products.push(doc.data());
          });
        }

        return products;
      });
    },
    sugProductsItemSelected: function(item) {
      if (this.orderItem.product.id == item.id) {
        this.orderItem.rndPrice = this.orderItem.rndPrice || item.rndPrice;
      } else {
        this.orderItem.rndPrice = item.rndPrice;
      }
    },
    setLabel: function(item) {
      return item.name;
    },
    saveOrderItem: function() {
      this.$emit("saveOrderItem", this.orderItem);
      this.$bvModal.hide(this.id);
    }
  }
};
</script>
