// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'core-js/es6/promise'
import 'core-js/es6/string'
import 'core-js/es7/array'
// import cssVars from 'css-vars-ponyfill'
import Vue from 'vue'
import BootstrapVue from 'bootstrap-vue'
import App from './App'
import router from './router'
import './shared/filters'
import axios from "axios"

import fb from "@/shared/firebaseConfig"

import VueCurrencyFilter from 'vue-currency-filter'
// todo
// cssVars()
Vue.prototype.$fb = fb;
Vue.prototype.vndConfig = {
  symbol: 'VND',
  thousandsSeparator: ',',
  fractionCount: 0,
  fractionSeparator: '.',
  symbolPosition: 'after',
  symbolSpacing: true
};

Vue.prototype.audConfig = {
  symbol: '$',
  thousandsSeparator: ',',
  fractionCount: 2,
  fractionSeparator: '.',
  symbolPosition: 'front',
  symbolSpacing: false
};
Vue.prototype.$http = axios;

Vue.prototype.appSettings = {
  commissionRate: 1.1, // 10%
  vndExchageRate: 16500,
  ctvCommissionRate: 1.08, //8%
  buyingFee: 2, // cong mua $2,
  //cwCheckPriceApi: "https://www.chemistwarehouse.com.au/cmsglobalfiles/handlers/predictive_search.ashx?term={term}",
  cwCheckPriceApi: "http://45.77.96.178/lamdam?term={term}",
  cwProductThumbnailUrl150: "https://static.chemistwarehouse.com.au/ams/media/pi/{id}/hero_150.jpg"

}

Vue.use(BootstrapVue)
Vue.use(VueCurrencyFilter);

let app = '';

fb.auth.onAuthStateChanged(() => {
  if (!app) {
    /* eslint-disable no-new */
    app = new Vue({
      el: '#app',
      router,
      template: '<App/>',
      components: {
        App
      }
    });
  }
});