<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <i class="cui-user icons font-lg"></i> Customers
          </div>
          <b-table
            responsive="sm"
            :items="customers"
            :fields="fields"
            :small="true"
            @row-clicked="customerClicked"
          >
            <template v-slot:cell(action)="data">
              <b-button type="submit" size="sm" variant="danger" @click="deleteCustomer(data.item)">
                <i class="fa fa-remove"></i> Delete
              </b-button>
            </template>
          </b-table>
        </b-card>
      </b-col>
    </b-row>
    <!--/.row-->
  </div>
</template>

<script>
import fb from "firebase";
import cTable from "@/views/table";

export default {
  name: "Settings",
  components: { cTable },
  data() {
    return {
      customers: [],
      customer: {},
      fields: [
        { key: "name", sortable: true },
        { key: "address" },
        { key: "phone" },
        { key: "action" }
      ]
    };
  },
  mounted: function() {
    this.loadCustomers();
  },
  computed: {
    notAllowSubmitNewCustomer: function() {
      return !this.customer || !this.customer.name;
    }
  },
  methods: {
    loadCustomers: function() {
      this.$fb.customerCollection.onSnapshot(snapshot => {
        this.customers = [];
        if (!snapshot.empty) {
          snapshot.forEach(doc => {
            var data = doc.data();
            data.id = doc.id;
            this.customers.push(data);
          });
        }
      });
    },
    submitNewCustomer: function() {
      if (!this.customer || !this.customer.name) return;
      if (this.customer.id) {
        this.$fb.customerCollection
          .doc(this.customer.id)
          .set(this.customer)
          .then(() => (this.customer = {}));
      } else {
        this.$fb.customerCollection
          .add(this.customer)
          .then(() => (this.customer = {}));
      }
    },
    customerClicked: function(customer) {
      this.customer = { ...customer };
    },
    resetCustomer: function() {
      this.customer = {};
    },
    deleteCustomer: function(customer) {
      if (!customer) return;
      this.$fb.customerCollection.doc(customer.id).delete();
    }
  }
};
</script>
