<template>
  <div class="animated fadeIn">
    <b-card>
      <div slot="header">Calculate Price</div>
      <b-form>
        <b-form-group label="Price ($A)">
          <b-form-input type="text" placeholder="Enter price" v-model="price"></b-form-input>
        </b-form-group>
        <b-form-group label="Weight(g)" description="100g = $1">
          <b-form-input type="text" placeholder="Enter weight" v-model="weight"></b-form-input>
        </b-form-group>
        <b-form-group label="Suggested Price (Price + 10% + Ship + 2)">
          ({{price | currency(audConfig)}} + {{price * 0.1 | currency(audConfig)}} + {{weight / 100 | currency(audConfig)}} + {{2 | currency(audConfig)}}) * {{16500 | currency(vndConfig)}}
          <strong>= {{sugPrice | currency(vndConfig)}}</strong>
        </b-form-group>
      </b-form>
    </b-card>
  </div>
</template>

<script>
export default {
  name: "Utils",
  components: {},
  data() {
    return {
      price: "",
      weight: ""
    };
  },
  mounted: function() {},
  computed: {
    sugPrice: function() {
      var price = parseFloat(this.price || 0);
      var weight = parseFloat(this.weight || 0);
      return (price + price * 0.1 + weight / 100 + 2) * 16500;
    }
  },
  methods: {}
};
</script>
