<template>
  <div class="animated fadeIn"></div>
</template>

<script>
import fb from "firebase";

export default {
  name: "Shipments",
  components: {},
  data() {
    return {
      order: {},
      orderItems: []
    };
  },
  mounted: function() {},
  computed: {},
  methods: {
    loadOrder: function() {
      this.$fb.orderCollection
        .doc(this.$route.params.id)
        .onSnapshot(snapshot => {
          if (!snapshot.empty) {
            this.order = snapshot.data();
            this.orderItems = _.chain(this.order.items)
              .groupBy(x => x.customer.name)
              .value();

            console.log(this.orderItems);
          }
        });
    }
  }
};
</script>
